import React from "react";
import './styles.scss';
import {cpfFormat} from '../../assist';

export default function cardPessoa(props) {
  const { change, dados, crianca, idade} = props;

  return (
    <div className="cardPessoa" onClick={change}> 
      <img src={dados.foto} alt='foto de perfil' className = "cardPessoa__img"/>

      {crianca === true ? 
        <div className="cardPessoa__divInfo">
          <p className="cardPessoa__dadosNome">{dados.nome}</p>
          <p className="cardPessoa__dados">Criana</p>
          {idade > 1 ? <p className="cardPessoa__dados">Idade: {idade} anos</p> :
          <p className="cardPessoa__dados">Idade: {idade} ano</p>}
          <p className="cardPessoa__dados">Responsvel: {dados.responsavel.nome}</p>
          {dados.responsavel.endereco === null ?
            <p className="cardPessoa__dados">O responsavel no possui endereo cadastrado</p>
          :
            <p className="cardPessoa__dados">Endereo: {dados.responsavel.endereco.logradouro}, {dados.responsavel.endereco.bairro}, {dados.responsavel.endereco.cidade}</p>
          }
        </div>
      :
        <div className="cardPessoa__divInfo">
          <p className="cardPessoa__dadosNome">{dados.nome}</p>
          <p className="cardPessoa__dados">Responsvel</p>
          {idade > 1 ? <p className="cardPessoa__dados">Idade: {idade} anos</p> :
          <p className="cardPessoa__dados">Idade: {idade} ano</p>}
          <p className="cardPessoa__dados">CPF: {cpfFormat(dados.cpf)}</p>
          {dados.endereco === null ?
            <p className="cardPessoa__dados">Essa pessoa no possui endereo cadastrado</p>
          :
            <p className="cardPessoa__dados">Endereo: {dados.endereco.logradouro}, {dados.endereco.bairro}, {dados.endereco.cidade}</p>
          }
        </div>
      }
    </div>
  );
}